.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  height: 30px;
  z-index: 1;
  /*animation*/
  transition-timing-function: ease-in;
  transition: all 0.5s; }
  .navbar .navbar__logo {
    position: fixed;
    left: 20px;
    width: 80px;
    object-fit: contain; }
  .navbar .navbar__avatar {
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
    background-color: #aaa;
    border-radius: 50%; }

.navbar__black {
  background-color: #111; }
